import React, { Component } from 'react'

export default class Header extends Component {
    render() {
        return (

            <div style={styles.headerBack} >
                <div style={styles.header}>
                    <img style={styles.picture} src="https://i.imgur.com/wbYWZA0.jpg" alt="Omar" />
                    <h1 style={styles.blogTitle} >Blog</h1>
                </div>
            </div>
        )
    }
}

const styles = {
    headerBack: {
        background: 'url(https://i.imgur.com/k5x66M5.jpg)',
        backgroundRepeat: "no-repeat",
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        borderRadius: '10px'
    },
    header: {
        width: '100%',
        height: '25em',
        marginBottom: '3em',
        textAlign: 'center',
        background: 'rgba(0,0,0,0.8)',
        borderRadius: '5px'
    },
    picture: {
        width: '10em',
        height: '10em',
        marginTop: '4.5em',
        borderRadius: '50%',
        padding: '10px',
        border: '5px solid #F8054D'
    },
    blogTitle: {
        color: "white",
        fontWeight: 'bold',
        marginTop: '1em',
    }
}